import {
  Component,
  OnInit
} from "@angular/core";
import { CartService } from "src/app/services/cart/cart.service";
import { Router, ActivatedRoute, NavigationEnd } from "@angular/router";
import { GalleryMenu } from "src/app/interfaces/interfaces";

@Component({
  selector: "app-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.scss"],
})
export class HeaderComponent implements OnInit {
  itemsInCart: Number = 0;
  menu: GalleryMenu[];
  transparentBackground: boolean = true;

  constructor(
    private cartService: CartService,
    private router: Router,
    private route: ActivatedRoute
  ) {
    router.events.subscribe(data => {
      if (data instanceof NavigationEnd) {
        debugger;
        if (data.urlAfterRedirects.includes("home")) {
          this.transparentBackground = true;
        } else {
          this.transparentBackground = false;
        }
      }
    })
  }

  ngOnInit() {
    this.cartService.cartSize.subscribe((itemsInCart) => {
      this.itemsInCart = itemsInCart;
    });
    this.menu = [
      {
        heading: "By Category",
        subheading: [
          { title: "Bars", link: "" },
          { title: "Buildings", link: "" },
          { title: "Chargers", link: "" },
          { title: "Entrances", link: "" },
          { title: "Games", link: "" },
          { title: "Lanterns & Lamps", link: "" },
          { title: "Misc Props", link: "" },
          { title: "Pillows", link: "" },
          { title: "Podiums", link: "" },
          { title: "Seating", link: "" },
          { title: "Tables", link: "" },
        ],
        columncount: 0,
      }
      // {
      //   heading: "Furniture",
      //   subheading: [
      //     { title: "Industrial", link: "" },
      //     { title: "Bars", link: "" },
      //     { title: "Columns", link: "" },
      //     { title: "Platforms", link: "" },
      //   ],
      //   columncount: 0,
      // },
      // {
      //   heading: "Event Essentials",
      //   subheading: [
      //     { title: "Buffet", link: "" },
      //     { title: "Carpet", link: "" },
      //     { title: "Dance Floor", link: "" },
      //     { title: "Flag", link: "" },
      //   ],
      //   columncount: 0,
      // },
      // {
      //   heading: "By Color",
      //   subheading: [
      //     { title: "Black", link: "" },
      //     { title: "Clear", link: "" },
      //     { title: "Green", link: "" },
      //     { title: "Orange", link: "" },
      //   ],
      //   columncount: 0,
      // },
    ];
  }

  displayCart() {
    console.log("Current cart: ", this.cartService.currentCart);
    this.router.navigate(["/cart"], { relativeTo: this.route });
  }

  fetch(category: string) {
    this.router.navigate(["/gallery"], {
      relativeTo: this.route,
      queryParams: {
        category: category,
      },
    });
  }
}
